import { Component, createEffect } from 'solid-js';
import { IColor, IIcon, NumberInput } from '../../../uikit/CabalExt';
import { createStore } from 'solid-js/store';
import { BuyPresetsValues } from './types';

export const BuyPresets: Component<{
  value: BuyPresetsValues;
  onChange: (newValues: BuyPresetsValues) => void;
}> = (props) => {
  const [store, setStore] = createStore<{ values: BuyPresetsValues }>({
    values: props.value,
  });

  createEffect(() => {
    props.onChange(store.values);
  });

  return (
    <div>
      <div class="e:flex e:gap-3">
        <div>
          <NumberInput
            value={store.values[0]}
            color={IColor.green}
            icon={IIcon.sol}
            onChange={(value) => {
              const newValues = [...store.values];
              newValues[0] = value;
              setStore('values', newValues);
            }}
          />
        </div>
        <div>
          <NumberInput
            value={store.values[1]}
            color={IColor.green}
            icon={IIcon.sol}
            onChange={(value) => {
              const newValues = [...store.values];
              newValues[1] = value;
              setStore('values', newValues);
            }}
          />
        </div>
      </div>
      <div class="e:flex e:gap-3 e:mt-2">
        <div>
          <NumberInput
            value={store.values[2]}
            color={IColor.green}
            icon={IIcon.sol}
            onChange={(value) => {
              const newValues = [...store.values];
              newValues[2] = value;
              setStore('values', newValues);
            }}
          />
        </div>
        <div>
          <NumberInput
            value={store.values[3]}
            color={IColor.green}
            icon={IIcon.sol}
            onChange={(value) => {
              const newValues = [...store.values];
              newValues[3] = value;
              setStore('values', newValues);
            }}
          />
        </div>
      </div>
    </div>
  );
};
